import UniIcons from '@/components/uni-icons/uni-icons.vue'
export default {
	name: 'EvanStep',
	components: {
		UniIcons
	},
	props: {
		title: {
			type: String,
			default: ''
		},
		description: {
			type: String,
			default: ''
		},
		// wait process finish error success
		status: {
			type: String,
			default: ''
		},
		icon: {
			type: String,
			default: ''
		}
	},
	computed: {
		direction() {
			const parent = this.getParent()
			return parent.direction
		},
		activeIndex() {
			const parent = this.getParent()
			return parent.active
		},
		primaryColor() {
			const parent = this.getParent()
			return parent.primaryColor
		},
		errorColor() {
			const parent = this.getParent()
			return parent.errorColor
		},
		isLast() {
			if (this.index === null) {
				return true
			}
			const parent = this.getParent()
			return parent.steps.length - 1 === this.index
		},
		currentStatus() {
			if (this.status) {
				return this.status
			}
			const parent = this.getParent()
			const active = parent.active
			if (this.index < active) {
				return 'finish'
			} else if (this.index === active) {
				return parent.status
			} else {
				return 'wait'
			}
		},
		nextStatus() {
			const parent = this.getParent()
			const steps = parent.steps
			if (this.index === steps.length - 1) {
				return ''
			}
			const nextIndex = this.index + 1
			if (steps && steps[nextIndex] && steps[nextIndex].status) {
				return steps[nextIndex].status
			}
			const active = parent.active
			if (nextIndex < active) {
				return 'finish'
			} else if (nextIndex === active) {
				return 'process'
			} else {
				return 'wait'
			}
		},
		circleStyle() {
			switch (this.currentStatus) {
				case 'finish':
					return {
						backgroundColor: '#fff',
						borderColor: this.primaryColor,
						color: this.primaryColor
					}
				case 'process':
					return {
						backgroundColor: this.primaryColor,
						borderColor: this.primaryColor,
						color: '#fff'
					}
				case 'wait':
					return {
						backgroundColor: '#ccc',
						borderColor: '#ccc',
						color: '#fff'
					}
				case 'error':
					return {
						backgroundColor: this.errorColor,
						borderColor: this.errorColor,
						color: '#fff'
					}
				default:
					return {
						backgroundColor: '#fff',
						borderColor: this.primaryColor,
						color: this.primaryColor
					}
			}
		},
		titleColor() {
			switch (this.currentStatus) {
				case 'finish':
					return 'rgba(0,0,0,0.65)'
				case 'process':
					return 'rgba(0,0,0,0.85)'
				case 'wait':
					return 'rgba(0,0,0,0.45)'
				case 'error':
					return this.errorColor
				default:
					return 'rgba(0,0,0,0.85)'
			}
		},
		descriptionColor() {
			switch (this.currentStatus) {
				case 'finish':
					return 'rgba(0,0,0,0.45)'
				case 'process':
					return 'rgba(0,0,0,0.65)'
				case 'wait':
					return 'rgba(0,0,0,0.45)'
				case 'error':
					return this.errorColor
				default:
					return 'rgba(0,0,0,0.85)'
			}
		},
		customIconColor() {
			switch (this.currentStatus) {
				case 'finish':
					return this.primaryColor
				case 'process':
					return this.primaryColor
				case 'wait':
					return '#ccc'
				case 'error':
					return this.errorColor
				default:
					return this.primaryColor
			}
		},
		lineColor() {
			switch (this.nextStatus) {
				case 'finish':
					return this.primaryColor
				case 'process':
					return this.primaryColor
				case 'wait':
					return '#ddd'
				case 'error':
					return this.errorColor
				default:
					return this.primaryColor
			}
		},
		contentHeight() {











			return 'auto'

		}
	},
	data() {
		return {
			index: null,
			customizeIcon: false,
			circleIconSize: 20,
			titleHeight: 0,
			descriptionHeight: 0
		}
	},
	methods: {
		getParent() {
			let parent = this.$parent
			let parentName = parent.$options.name
			while (parentName !== 'EvanSteps') {
				parent = parent.$parent
				parentName = parent.$options.name
			}
			return parent
		}
	},
	mounted() {
		this.customizeIcon = this.$scopedSlots.icon || false
		const parent = this.getParent()
		this.index = parent.steps.length
		parent.steps.push({
			title: this.title,
			description: this.description,
			status: this.status
		})

		this.circleIconSize = 20





























	}
}
